@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;700&display=swap);
.currentWeatherContainer {
    height: 33%;
}

.location-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 0 5px;
} 
    .location-info h2 {
        margin: 5px;
    }

.row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

    .row span { 
        font-size: .9rem;
    }

.main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}
    .main h2 {
        font-size: 4rem;
        font-weight: 700;
        margin: 5px;
    }
    .main h4 { 
        font-size: 18px;
        margin: 0;
    }
    .main span {
        font-size: 16px;
    }
.sub-main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.sun-settings {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#compass {
    background: url(/static/media/Compass.7737f1e1.png); 
    background-size: cover;
  }
.dayForecastComponent {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    height: 33%;
    margin: 1rem;
}

.day {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center; 
    color: white;
    width: 12.5%;
}
    .day h3 { color: rgb(251, 255, 5); }
    .day img { height: 75px }
    .day > * {
        margin:  0;
        padding: 0;
    }


.dayHighLow {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    line-height: 0;
}

.sun-settings {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    line-height: 0;
}
.hourlyForecastComponent {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 33%;
    overflow-y: hidden;
    overflow-x: auto;
    margin: 1rem;
}

.hour {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center; 
    color: white;
    margin: 0px 25px;
}
.hour > * {
    margin: 5px 0;
}
.bottom-section {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
}
    .bottom-section h4 {
        margin: 0 10px;
    }
#container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: white;
    font-family: 'Space Grotesk', sans-serif;
}
.weather-container {
    width: 85vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 20px;
    background-position: bottom;
    background-size: cover;
}

/*---Search Bar---*/

.search-btn {
    background: transparent;
    border: none;
    font-size: 25px;
    vertical-align: middle;
    cursor: pointer;
}

.bar {
    position: relative;
    top: 20%;
    margin: 0 auto;
    width: 41%;
    border-radius: 30px;
    border: 1px solid #dcdcdc;
    margin: 2rem 0;
}
.bar:hover{
    box-shadow: 1px 1px 8px 1px #dcdcdc;
}
.bar:focus-within{
    box-shadow: 1px 1px 8px 1px #dcdcdc;
    outline:none;
}
.searchbar{
    height:50px;
    border:none;
    width:500px;
    font-size:16px;
    outline: none;
}
